import React from 'react'
import vs from './images/vs.png'
import home from './images/cm.png'
import user from '../../Components/Media/user.png'
import logo1 from '../../Components/Media/logo1.png'
import maharashtra from '../../Components/Media/maharashtra.jpg'
import cm from '../../Components/Media/cm.jpg'

const Branding = () => {
  return (
    <>
       <div 
       className='relative w-full h-full'>
                <div className='absolute h-full w-full top-0 ' style={{ backgroundImage: `url(${maharashtra})`, backgroundSize: 'cover', backgroundRepeat: 'repeat', backgroundPositionY : '-60vh', opacity: '15%', zIndex: 0 }}></div>
                <div className='flex justify-between px-2 sm:px-20 py-1.5 items-center' style={{zIndex : 999}}>
                  <img src={logo1} alt="" className='w-16 sm:w-20 h-16 sm:h-20 block border-2 shadow-md rounded-full' />
                  <div className='flex flex-col items-center justify-center'>
                    <span className='text-sm sm:text-4xl font-bold text-center uppercase'>Akola Municipal Corporation</span>
                    <span className='text-xs sm:text-base'>All Public Services At Your Fingertip</span>
                  </div>
                  <img src={cm} alt="" className='w-16 sm:w-20 h-16 sm:h-20 block border-2 shadow-md rounded-full opacity-100' />
                </div>
        </div>
                
    </>
  )
}

export default Branding