import React from 'react'
import { BsQuestionCircle } from 'react-icons/bs'
import { Tooltip } from 'react-tooltip'

const Card = (props) => {
  return (
    <>
    
    <div onClick={() => window.location.replace(props?.route)}
                                className="bg-gray-100 select-none rounded-xl flex flex-col justify-center items-center p-4 md:p-6 relative cursor-pointer hover:scale-105 hover:shadow-md transition-all duration-150"
                                href="">
                                {/* <Tooltip style={{ 'zIndex': 1000 }} anchorId="card" />
                                <span className='absolute top-2 right-2'><BsQuestionCircle onClick={(e) => activateInfoCard(e, 1)} id="card1" data-tooltip-content="Click to know more about new assessment." className="inline text-red-500 text-xl font-semibold cursor-pointer hover:text-red-800" /></span> */}
                                <img
                                    className="h-16 w-16"
                                    src={props?.image}
                                    alt="Mobiles" />
                                <div className="font-bold mt-4 text-center">{props?.name}</div>
                                {/* <div className="mt-1 text-center w-full text-xs px-4">Choose this for fresh assessment of property.</div> */}
                            </div>
    
    </>
  )
}

export default Card